<template>
    <div class="container">
        <div class="main__info">
            <div class="main__info-left">
                <h2>{{ getCar.name }}</h2>
                <p>{{ getCar.text }}</p>
            </div>
            <div class="main__info-right">
                <img :src="getCar.img" alt="">
            </div>
        </div>
    </div>
</template>

<script>
import data from '@/data'
    export default {
        data() {
            return {
                cars: data,
                currentId: null
            }
        },
        created() {
            this.currentId = this.$route.params.id
        },
        computed: {
            getCar() {
                return this.cars.find((car) => car.id == this.currentId)
            }
        }
    }
</script>

